import { BaseMidiEventEmitter } from '@/services/BaseMidiEventEmitter';
import { getSimultaneousEvents } from '@/services/MidiFileAdapter';

const getEventsSlice = (start, end, events) => {
  // start and end params assume only key press events,
  // but events list may contain other events
  // so Array.prototype.slice() will not work for this case
  let keyPressCount = 0;
  const sliced = [];

  events.forEach((event) => {
    if (keyPressCount >= start && keyPressCount < end) {
      sliced.push(event);
    }

    if (event.hasKeyPressEvents()) {
      keyPressCount += 1;
    }
  });

  return sliced;
};

export class MidiFileEventEmitterFreeTempo extends BaseMidiEventEmitter {
  #fileData;
  #tracks;
  #fullListOfEvents;
  #events;
  #lastEvent;
  #from;
  #to;

  constructor(fileData, options) {
    super();

    this.#fileData = fileData;
    this.#tracks = options?.tracks || 'both';
    this.#fullListOfEvents = getSimultaneousEvents(this.#fileData, { tracks: this.#tracks });
    const from = options?.from || 0;
    const to = options?.to || this.#fullListOfEvents.length;

    this.#setRange({ from, to });
    this.#init();
  }

  #init() {
    this.#events = getEventsSlice(this.#from, this.#to, this.#fullListOfEvents);
    this.#events.reverse();

    this.#lastEvent = null;
  }

  play() {
    const event = this.#events.pop();
    this.#lastEvent = event;

    if (event) {
      this.dispatchEvent(event);
    }

    if (!event) {
      this.dispatchEvent('end');

      return;
    }

    if (event && !event.hasKeyPressEvents()) {
      this.play();
    }
  }

  // eslint-disable-next-line no-dupe-class-members
  #setRange({ from = null, to = null }) {
    if (from !== null) {
      this.#from = from;
    }

    if (to !== null) {
      this.#to = to;
    }
  }

  restart({ from, to }) {
    this.#setRange({ from, to });
    this.#init();
  }

  getLastEvent() {
    return this.#lastEvent;
  }
}
