<template>
  <div ref="staves" class="staves page__staves">
    <div v-if="!restarting" id="staves" class="staves__target"></div>
  </div>
</template>

<script>
import * as opensheetmusicdisplay from 'opensheetmusicdisplay';
import lodash from 'lodash';
import { sleep } from '@/utils';

export default {
  name: 'Staves',
  props: {
    selectedSong: {
      type: Object,
      required: true,
    },
    from: {
      type: Number,
      default: 0,
    },
    to: {
      type: Number,
      default: null,
    },
  },
  data() {
    return {
      cursorStep: 35,
      osmd: null,
      pitchHasStartedTie: {},
      restartTime: 0,
      voiceEntryIndex: null,
      restarting: false,
    };
  },
  computed: {
    tracks() {
      return this.$route.query?.tracks || 'both';
    },
  },
  watch: {
    selectedSong: 'loadSong',
  },
  async mounted() {
    await this.init();
  },
  methods: {
    async init() {
      const osmd = new opensheetmusicdisplay.OpenSheetMusicDisplay('staves');
      osmd.setOptions({
        backend: 'svg',
        drawTitle: false,
        drawMeasureNumbers: true,
        drawPartNames: false,
        cursorsOptions: [
          {
            type: 0,
            color: '#ff0000',
            alpha: 0.5,
          },
        ],
      });
      this.osmd = osmd;

      await this.loadSong();

      this.voiceEntryIndex = 0;

      if (this.shouldPauseAnyTrack() && this.shouldSkipEntry()) {
        this.voiceEntryIndex += 1;
        this.play();

        return;
      }

      if (!this.shouldPauseAnyTrack()) {
        this.play();
      }
    },
    shouldSkipEntry() {
      return this.voiceEntryIndex < this.from;
    },
    isEndReached() {
      if (!this.osmd.cursor) {
        return true;
      }

      const hasRightLimit = typeof this.to === 'number';

      return (
        this.osmd.cursor.iterator.endReached || (hasRightLimit && this.voiceEntryIndex >= this.to)
      );
    },
    play() {
      if (this.isEndReached()) {
        return;
      }

      this.moveCursor();

      if (this.shouldPauseAnyTrack()) {
        if (!this.shouldSkipEntry()) {
          return;
        }

        this.voiceEntryIndex += 1;
      }

      this.play();
    },
    shouldPauseAnyTrack() {
      const voiceEntryTrackA = lodash.get(this.osmd.cursor, 'iterator.currentVoiceEntries.0', null);
      const voiceEntryTrackB = lodash.get(this.osmd.cursor, 'iterator.currentVoiceEntries.1', null);

      if (this.tracks === 'first') {
        return this.shouldPauseTrack(voiceEntryTrackA);
      }

      if (this.tracks === 'second') {
        return this.shouldPauseTrack(voiceEntryTrackB);
      }

      return this.shouldPauseTrack(voiceEntryTrackA) || this.shouldPauseTrack(voiceEntryTrackB);
    },
    shouldPauseTrack(voiceEntry) {
      if (!voiceEntry) {
        return false;
      }

      const notes = lodash.get(voiceEntry, 'notes', []).filter((note) => {
        const isNotRest = !!lodash.get(note, 'pitch.halfTone', null);

        return isNotRest;
      });

      if (notes.length === 0) {
        // only rests
        return false;
      }

      const noTies = notes
        .map((note) => {
          const hasTieNotes = !!lodash.get(note, 'tie.notes', null);

          return hasTieNotes;
        })
        .every((v) => v === false);

      if (noTies) {
        // only normal notes
        return true;
      }

      const tiePitches = notes
        .map((note) => lodash.get(note, 'tie.notes.0.pitch.halfTone', null))
        .filter((v) => v !== null);
      const justStartedTiePitches = tiePitches.filter((pitch) => !this.pitchHasStartedTie[pitch]);
      const justEndedTiePitches = tiePitches.filter((pitch) => this.pitchHasStartedTie[pitch]);

      if (justStartedTiePitches.length > 0) {
        // has notes with ties, but it's a tie start
        justStartedTiePitches.forEach((pitch) => {
          this.pitchHasStartedTie[pitch] = true;
        });

        justEndedTiePitches.forEach((pitch) => {
          this.pitchHasStartedTie[pitch] = false;
        });

        return true;
      }

      justEndedTiePitches.forEach((pitch) => {
        this.pitchHasStartedTie[pitch] = false;
      });

      return false;
    },
    async restart() {
      this.restarting = true;
      await sleep(50);
      this.restarting = false;

      await this.$nextTick();
      await this.init();
    },
    moveCursor() {
      this.osmd.cursor.next();
      const scrollPosition = this.$refs.staves.scrollLeft + this.cursorStep;
      this.$refs.staves.scroll(scrollPosition, 0);
    },
    async loadSong() {
      await this.osmd.load(this.selectedSong.xmlUrl);
      this.osmd.render();
      this.osmd.cursor.show();
    },
  },
};
</script>

<style scoped>
.staves {
  width: 100%;
  overflow-x: scroll;
}

.staves__target {
  width: 32000px;
  padding-left: calc(50vw - 100px);
}
</style>
